import { createApp } from 'vue';
import App from './App.vue';
import { createRouter, createWebHistory } from 'vue-router';
import 'bootstrap/dist/css/bootstrap.min.css';  // Import Bootstrap CSS
import 'materialize-css/dist/css/materialize.min.css';  // Import Materialize CSS
import 'materialize-css';  // Initialize Materialize JS

import HomeView from './views/HomeView.vue';

const routes = [
    { path: '/', component: HomeView },
    { path: '/setup', component: () => import('./components/Setup.vue') },  // Use dynamic import for better code splitting
    // Add more routes as needed
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

const app = createApp(App);
app.use(router);
app.mount('#app');
