<template>
  <div class="container mt-4">
    <h1 class="mb-4">Spiel Einstellungen: GameID {{ userData.id }} PlayerNumber {{ getPlayerNumber() }}</h1>

    <div class="card">
      <div class="card-body">
        <h5 class="card-title">Playername: {{ userData.username }}</h5>
        <p class="card-text">Shrimp 1: {{ getShrimpName(1) }}</p>
        <p class="card-text">Shrimp 2: {{ getShrimpName(2) }}</p>
        <p class="card-text">Shrimp 3: {{ getShrimpName(3) }}</p>
      </div>
    </div>

    <!-- Display connected clients -->
    <div class="card mt-4">
      <div class="card-body">
        <h5 class="card-title">Connected Clients</h5>
        <ul>
          <li v-for="client in connectedClients" :key="client.id">{{ client.username }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { fetchData } from "@/api.js";
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';

export default {
  created() {
    this.setupWebSocket();
  },

  data() {
    return {
      userData: {
        id: null,
        playerStatisticsList: [],
        roundToSuddenDeath: -1,
        roundTimeLimit: -1,
        gameState: "INITIALIZATION",
        foregroundMask: null,
        levelForeground: null,
        levelBackground: null,
        // Add other properties as needed...
      },
      connectedClients: [],
    };
  },

  methods: {
    setupWebSocket() {
      const socket = new SockJS("http://localhost:8080/register_socket");
      this.stompClient = Stomp.over(socket);

      this.stompClient.connect({}, () => {
        console.log("WebSocket connection opened");

        // Subscribe to a topic for online users
        this.stompClient.subscribe("/lobby/onlineUsers", (payload) => {
          this.onConnectedUsersUpdated(JSON.parse(payload.body));
        });

        // Perform any additional subscriptions or initialization here
        // For example, you might subscribe to other topics or send an initial message to the server

        // Call the method to fetch initial user data
        this.fetchData();
      });
    },

    onConnectedUsersUpdated(updatedUsers) {
      console.log("Updated online users:", updatedUsers);
      // Update your component's data with the updated user list
      this.connectedClients = updatedUsers;
    },

    fetchData() {
      // Fetch user data from the API
      fetch("http://localhost:8080/game/startNewGame")
          .then((response) => response.json())
          .then((data) => {
            // Store user data for later use
            this.userData = data;

            // Load and process other data as needed

            // Connect to the WebSocket
            this.setupWebSocket();
          })
          .catch((error) => console.error("Error fetching user data:", error));
    },

    getPlayerNumber() {
      // Find the user's playerStatistics entry in the list
      const userPlayerStat = this.userData.playerStatisticsList.find(
          (playerStat) => playerStat.playerDTO.username === this.userData.username
      );

      // If the user's playerStatistics entry is found, return its position + 1 as the player number
      if (userPlayerStat) {
        const playerNumber = this.userData.playerStatisticsList.indexOf(userPlayerStat) + 1;
        return playerNumber;
      }

      // If the user's playerStatistics entry is not found, return a default value (e.g., 1)
      return 1;
    },

    getShrimpName(shrimpNumber) {
      const shrimpList = this.userData.playerStatisticsList[0]?.shrimpDTOList;

      if (shrimpList && shrimpList.length >= shrimpNumber) {
        return shrimpList[shrimpNumber - 1].name;
      }

      return "N/A";
    },
  },
};
</script>

<style scoped>
