<template>
  <div>
    <div id="gameHeader">
      <h2>Welcome, {{ userData.username }}</h2>
    </div>
    <div id="gameContainer">
      <div>
        <canvas ref="canvasOriginal" :width="canvasWidth" :height="canvasHeight"></canvas>
        <canvas ref="canvasForegroundMask" :width="canvasWidth" :height="canvasHeight"></canvas>
        <canvas ref="canvasProcessed" :width="canvasWidth" :height="canvasHeight"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';
import {fetchData} from "@/api.js";

export default {
  components: {
    HeaderComponent,
  },
  data() {
    return {
      userData: {},
      stompClient: null,
      canvasWidth: 1366,
      canvasHeight: 786,
    };
  },
  mounted() {
    fetchData()
        .then((data) => {
          this.userData = data;
          this.setupWebSocket();
          this.loadAndProcessImages();
        })
        .catch((error) => console.error("Error fetching user data in Game:", error));

  },
  methods: {
    fetchData() {
      return fetch("http://localhost:8080/game/startNewGame")
          .then((response) => response.json())
          .catch((error) => {
            console.error("Error fetching user data:", error);
            throw error; // Propagate the error
          });
    },
    setupWebSocket() {
      // Connect to the WebSocket endpoint
      const socket = new SockJS("http://localhost:8080/register_socket");
      this.stompClient = Stomp.over(socket);

      this.stompClient.connect({}, () => {
        console.log("WebSocket connection opened");
      });
    },
    onProcessedImageReceived(processedImage) {
      const canvasProcessed = this.$refs.canvasProcessed;
      const contextProcessed = canvasProcessed.getContext('2d');
      contextProcessed.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
      const imgProcessed = new Image();
      imgProcessed.src = processedImage;
      imgProcessed.onload = () => {
        contextProcessed.drawImage(imgProcessed, 0, 0, this.canvasWidth, this.canvasHeight);
      };
    },
    loadAndProcessImages() {
      // Load the original image
      const originalImage = new Image();
      originalImage.src = '/levels/level0_foreground_fill_dirty.png';
      originalImage.onload = () => {
        // Create a canvas for the original image
        const canvasOriginal = this.$refs.canvasOriginal;
        const contextOriginal = canvasOriginal.getContext('2d');
        contextOriginal.drawImage(originalImage, 0, 0);

        // Extract new alpha mask information from this.userData.foregroundMask
        const alphaMaskImage = new Image();
        alphaMaskImage.src = 'data:image/png;base64,' + this.userData.foregroundMask;
        alphaMaskImage.onload = () => {
          // Create a canvas for the foreground mask
          const canvasForegroundMask = this.$refs.canvasForegroundMask;
          const contextForegroundMask = canvasForegroundMask.getContext('2d');
          contextForegroundMask.drawImage(alphaMaskImage, 0, 0);

          // Create a canvas for the processed image
          const canvasProcessed = this.$refs.canvasProcessed;
          const contextProcessed = canvasProcessed.getContext('2d');

          // Draw the original image with updated alpha onto the processed canvas
          const imageDataOriginal = contextOriginal.getImageData(0, 0, this.canvasWidth, this.canvasHeight);
          const imageDataAlpha = contextForegroundMask.getImageData(0, 0, this.canvasWidth, this.canvasHeight);

          for (let i = 0; i < imageDataOriginal.data.length; i += 4) {
            // Replace the alpha value with the new alpha mask value
            imageDataOriginal.data[i + 3] = imageDataAlpha.data[i + 3];
          }

          // Draw the modified image back to the processed canvas
          contextProcessed.putImageData(imageDataOriginal, 0, 0);

          // Convert the processed canvas content to base64 for display
          const processedImageUrl = canvasProcessed.toDataURL();

          // Set the processed image URL for display
          this.onProcessedImageReceived(processedImageUrl);

          // Getting Informaions about Players objects
          const playerStatsList = this.userData.playerStatisticsList;
          // Getting Shrimps, while developing one Single Shrimp
          if (playerStatsList && playerStatsList.length > 0) {
            const shrimpList = playerStatsList[0]?.shrimpDTOList;

            if (shrimpList && shrimpList.length > 0) {
              const firstShrimp = shrimpList[0];

              // Create a canvas for the shrimp image
              const canvasShrimp = this.$refs.canvasProcessed; // Use the processed canvas as the shrimp canvas
              const contextShrimp = canvasShrimp.getContext('2d');

              // Draw the shrimp image onto the processed canvas with 1/4 size
              const shrimpImage = new Image();
              shrimpImage.src = '/elements/shrimp_fill.png';
              shrimpImage.onload = () => {
                // Calculate scaled dimensions
                const scaledWidth = shrimpImage.width / 4;
                const scaledHeight = shrimpImage.height / 4;
                // Position for Shrimp, to be edited after GameState is finished
                const shrimpDx = 200;
                const shrimpDy = 200;
                // Draw the shrimp image onto the processed canvas with the new size
                contextShrimp.drawImage(shrimpImage, shrimpDx, shrimpDy, scaledWidth, scaledHeight);

                // Display name and hitpoints in a box above the shrimp, centered
                const shrimpName = firstShrimp.name;
                const shrimpHitpoints = firstShrimp.hitpoints;
                const text = `${shrimpName}: ${shrimpHitpoints}`;
                contextShrimp.font = '16px Arial';
                // Calculate the position for centering the text
                const textWidth = contextShrimp.measureText(text).width;
                const boxX = shrimpDx + (scaledWidth - textWidth) / 2; // Center horizontally
                const boxY = shrimpDy -20;
                const boxWidth = textWidth + 10;
                const boxHeight = 20;
                contextShrimp.fillStyle = 'white';
                // Draw a background box
                contextShrimp.fillRect(boxX, boxY, boxWidth, boxHeight);
                contextShrimp.fillStyle = 'black';
                contextShrimp.fillText(text, boxX + 5, boxY + 15); // Adjust the text baseline for vertical centering
              };
            } else {
              console.error('No shrimps found in shrimpDTOList.');
            }
          } else {
            console.error('No player statistics found in playerStatisticsList.');
          }
        };
      };
    },

  },
};
</script>

<style scoped>
#gameHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#gameContainer {
  margin-top: 20px;
}
</style>
