<template>
  <div>
    <div id="lobbyHeader">
      <h2>Welcome Home, {{ userData.username }}</h2>
    </div>
    <Chat :userData="userData" />
  </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
import Chat from "@/components/Chat.vue";


export default {
  components: {
    HeaderComponent,
    Chat,
  },
  data() {
    return {
      userData: {},
    };
  },
  mounted() {
    fetch("http://localhost:8080/myPlayer")
        .then((response) => response.json())
        .then((data) => {
          this.userData = data;
        })
        .catch((error) => console.error("Error fetching user data:", error));
  },
};
</script>

<style scoped>
#lobbyHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
