<template>
  <div class="online-users">
    <!-- Display online users -->
    <div v-if="onlineUsers && onlineUsers.length">
      <div v-for="user in onlineUsers" :key="user.id">
        {{ user.username }}
      </div>
    </div>
    <div v-else>
      No online users.
    </div>
  </div>
</template>

<script>
export default {
  props: {
    onlineUsers: Array,
  },
  methods: {
    // Method to update the onlineUsers prop
    updateOnlineUsers(newOnlineUsers) {
      this.$emit("updateOnlineUsers", newOnlineUsers);
    },
  },
  watch: {
    onlineUsers: {
      handler(newOnlineUsers) {
        // Check if onlineUsers is an array and not empty
        if (Array.isArray(newOnlineUsers) && newOnlineUsers.length > 0) {
          // Handle the updated online users data
          console.log("Online Users Updated:", newOnlineUsers);
        } else {
          console.log("Online Users Updated: No online users.");
        }
      },
      deep: true, // Enable deep watching of the array
      immediate: true, // Trigger the handler immediately on component mount
    },
  },
};
</script>

