<template>
  <div id="chatContainer" class="container-md">
    <div class="row">
      <!-- Chat messages taking up 80% of the space -->
      <div class="col-md-8">
        <div class="chat-messages" ref="chatMessages">
          <!-- Your existing chat messages rendering logic here -->
          <div v-for="message in messages.slice(-20)" :key="message.id">
            <p>
              <span>{{ formatTimestamp(message.timestamp) }}</span>
              <span>{{ message.sender.username }}:</span>
              <span>{{ message.text }}</span>
            </p>
          </div>
        </div>
        <!-- Input field for sending messages -->
        <div class="chat-input">
          <input v-model="newMessage" @keyup.enter="sendMessage" class="form-control m6" />
          <button class="btn btn-primary m2" @click="sendMessage">Send</button>
        </div>
      </div>
      <!-- Online Users window, taking up 20% of the space -->
      <div class="col-md-4">
        <div class="online-users">
          <OnlineUsers :onlineUsers="onlineUsers" @updateOnlineUsers="handleUpdateOnlineUsers" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Stomp from 'stompjs';
import SockJS from 'sockjs-client';
import OnlineUsers from "@/components/OnlineUsers.vue";

export default {
  components: {
    OnlineUsers,
  },
  props: {
    userData: Object,
  },
  data() {
    return {
      messages: [],
      newMessage: "",
      stompClient: null,
      onlineUsers: [],
    };
  },
  mounted() {
    this.setupWebSocket();
    this.scrollToBottom();
    this.getCurrentUsers();
  },
  watch: {
    messages() {
      this.scrollToBottom();
    },
  },
  methods: {
    setupWebSocket() {
      const socket = new SockJS("http://localhost:8080/register_socket");
      this.stompClient = Stomp.over(socket);

      this.stompClient.connect({}, () => {
        console.log("WebSocket connection opened");
        this.stompClient.subscribe("/chat/messages", (payload) => {
          this.onMessageReceived(JSON.parse(payload.body));
        });
        this.stompClient.send("/shrimps/socket.addUser", {}, {});
        this.getChatHistory();
        this.getCurrentUsers();
      });
    },
    onMessageReceived(message) {
      this.messages.push(message);
      this.getCurrentUsers();
      // Check if the message indicates a user leaving the chat
      if (message.text.includes("hat den Chat verlassen")) {
        console.log("User left. Fetching updated online users...");
        setTimeout(() => {
          this.getCurrentUsers(); // Fetch updated online users
        }, 1000);
      }
    },
    getChatHistory() {
      fetch("http://localhost:8080/lobby/getAllLobbyChat")
          .then((response) => response.json())
          .then((data) => {
            this.addPreviousMessagesToDatasource(data.reverse());
          })
          .catch((error) => console.log(error));
    },
    addPreviousMessagesToDatasource(data) {
      this.messages = data.slice(-20).reverse();
    },
    sendMessage() {
      try {
        const message = {
          text: this.newMessage,
          sender: {
            id: this.userData.id,
            email: this.userData.email,
            username: this.userData.username,
          },
          timestamp: Date.now(),
        };
        this.stompClient.send("/shrimps/socket.sendMessage", {}, JSON.stringify(message));
      } catch (error) {
        console.error("Error in sendMessage:", error);
      }
      this.newMessage = "";
    },
    formatTimestamp(timestamp) {
      const time = new Date(timestamp);
      return `${time.getDate()}.${time.getMonth() + 1}.${time.getFullYear()} ${time
          .getHours()
          .toString()
          .padStart(2, "0")}:${time.getMinutes().toString().padStart(2, "0")}`;
    },
    scrollToBottom() {
      const chatMessages = this.$refs.chatMessages;
      if (chatMessages) {
        this.messages.reverse();
        chatMessages.scrollTop = chatMessages.scrollHeight;
      }
    },
    getCurrentUsers() {
      fetch("http://localhost:8080/lobby/onlineUsers")
          .then((response) => response.json())
          .then((data) => {
            if (Array.isArray(data)) {
              // Update onlineUsers only if there's a change in the user list
              if (!this.areArraysEqual(this.onlineUsers, data)) {
                this.onlineUsers = data;
              }
            } else {
              console.error("Invalid onlineUsers data format:", data);
            }
          })
          .catch((error) => {
            console.error("Error fetching online users:", error);
          });
    },
// Add a method to compare arrays for equality
    areArraysEqual(array1, array2) {
      return JSON.stringify(array1) === JSON.stringify(array2);
    },
    handleUpdateOnlineUsers(newOnlineUsers) {
      // Update the local onlineUsers data when the OnlineUsers component emits an update
      this.onlineUsers = newOnlineUsers;
    },
  },
};
</script>

<style scoped>
#chatContainer {
  margin-top: 20px;
}

.chat-messages {
  max-height: 600px;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
}

.chat-messages p {
  margin: 5px 0;
}

.col-md-8 {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 20px;
}

.chat-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn-primary {
  width: 20%;
}

.col-md-4 {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 20px;
}

</style>