// api.js

export function fetchData() {
    console.log("Fetching user data...");
    return fetch("http://localhost:8080/game/startNewGame")
        .then((response) => response.json())
        .catch((error) => {
            console.error("Error fetching user data:", error);
            throw error; // Propagate the error
        });
}
// api.js
export async function getCurrentUsers() {
    try {
        const response = await fetch("http://localhost:8080/lobby/onlineUsers");
        const data = await response.json();

        if (Array.isArray(data)) {
            return data;
        } else {
            console.error("Invalid onlineUsers data format:", data);
            return [];
        }
    } catch (error) {
        console.error("Error fetching online users:", error);
        throw error;
    }
}

